.footer {position: relative;background: #16181E;padding: 20px 0;width: 100%;}.copyright-text p {font-size: 14px;color: #ddd;margin-bottom: 0;font-weight: 300;line-height: 28px;}.copyright-text p a {color: #b9a11e;font-weight: 400;}.footer-top {display: flex;justify-content: space-between;align-items: center;}.footer-top img {height: 48px;margin: 0px 5px;object-fit: cover;max-width: 150px;width: 100%;}.footer .google-btns {display: flex;}.footer .privacy-links a {font-weight: 400;font-size: 16px;line-height: 24px;color: #CECECE;}.footer .privacy-links a:first-child{margin-right: 42px;}@media screen and (max-width: 767px){.copyright-text p {font-size: 12px;}}