.header {
  position: fixed;
  z-index: 999;
  width: 100%;
  transition: all ease 0.2s;
  top: 0;
}
.header.sticky-header {
  background: #1a1c22;
  z-index: 9999999999;
  box-shadow: 0px 6px 25px rgba(96, 96, 96, 0.1);
}
.header .logo {
  max-width: 183px;
  width: 100%;
}
.header .logo img {
  cursor: pointer;
}
.bg-dark {
  background-color: #191b25 !important;
}
.main-wrapper {
  overflow-x: hidden;
}
.header .navbar {
  padding: 18px 16px;
}
.header-logo img {
  width: 90px;
}
.header-top-login ul li a:hover {
  text-decoration: none;
}
.header-logo span {
  color: #fff;
  padding-left: 5px;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.5px;
  font-size: 70%;
}
.header-top-area {
  padding: 0px 0px;
  width: 100%;
  top: 0;
}
.header-top-login ul {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
}
.header-top-login.before-login ul {
  justify-content: flex-end;
}
.header-top-login ul li span {
  display: block;
  background: #191b25;
  text-transform: uppercase;
  color: #cfcdcd;
  font-weight: 400;
  letter-spacing: 0.5px;
  font-size: 12px;
  padding: 5px 20px;
  border-top: 1px solid #2a2b37;
  border-bottom: 1px solid #2a2b37;
  border-radius: 30px;
  cursor: pointer;
  min-width: 100px;
  line-height: 16px;
  text-align: center;
  transition: all 0.5s;
  box-shadow: rgb(0 0 0 / 35%) 0px 3px 7px 0px;
}
.header-top-login ul li span a {
  color: #fff;
}
.header-top-login ul li span a:hover,
.header-top-login ul li span a:focus {
  color: #fff;
  text-decoration: none;
}
.activeheader i {
  color: #b9a11e !important;
}
.header-top-login ul li span:hover {
  background: #b9a11e;
  color: #fff;
  border-top: 1px solid #b9a11e;
  border-bottom: 1px solid #b9a11e;
  box-shadow: 0 0 50px 0 hsl(0deg 0% 100% / 50%);
}
.header-top-login ul li span i {
  width: 29px;
  height: 25px;
  box-shadow: 0px 3px 7px 0px rgb(128 18 40 / 20%),
    inset 0px 3px 7px 0px rgb(56 17 33 / 40%);
  text-align: center;
  line-height: 25px;
  background: #1d1e29;
  margin-right: 15px;
  color: #ff5917;
}
.header-top-login ul li.or {
  margin: 0 10px;
  font-size: 11px;
  text-transform: uppercase;
  font-weight: 700;
  color: #d4d4d4;
}
ul {
  margin: 0 !important;
  list-style-type: none !important;
  padding: 0 !important;
}
.header-top-login ul li {
  margin: 0px 10px;
}
.navbar .container {
  display: block !important;
}
.show-mob .notification-box.box .notifications {
  display: none !important;
}
.video-popup {
  max-width: 370px;
  position: fixed;
  bottom: 15px;
  left: 15px;
  background: #1a1c22;
  z-index: 99;
  padding: 15px 20px;
  border-radius: 5px;
  box-shadow: 0 0 20px rgb(0 0 0 / 25%);
}
.video-box {
  width: 100%;
  height: auto;
}
.video-popup h2 {
  font-size: 16px;
  color: rgb(255, 255, 255);
  text-align: left;
  padding-bottom: 15px;
  line-height: 1.5;
  font-weight: 600;
  margin: 0;
  justify-content: space-between;
  display: flex;
  align-items: center;
}
.video-popup h2 span img {
  width: 16px;
  cursor: pointer;
  opacity: 0.7;
}
.video-popup p {
  font-size: 14px;
  color: rgb(255, 255, 255);
  text-align: left;
  padding-bottom: 0;
  line-height: 1.5;
  font-weight: 300;
  padding-top: 5px;
  max-width: 300px;
}
.video-box video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.in-game-popup {
  height: 200px;
  padding: 25px;
  text-align: center;
}
.in-game-popup .leave-game {
  border-radius: 20px !important;
  height: 40px;
  background: #dc3545;
  border: 1px solid white;
  margin-right: 5px;
}
.in-game-popup .leave-game:hover {
  background: #ac1524;
  box-shadow: 0 0 50px 0 hsl(0deg 0% 100% / 30%) !important;
  border: 1px solid white;
  margin-right: 5px;
}
.in-game-popup h6 {
  font-size: 20px;
  margin-bottom: 28px;
}
.in-game-popup .action {
  display: flex;
  justify-content: center;
}
.rejoin-class {
  margin-left: 5px !important;
  border: 1px solid white !important;
}
.in-game .action button {
  padding: 10px 25px;
  border-radius: 54px !important;
  min-width: 150px;
  height: 45px;
}
.in-game .action .spinner-border {
  margin-top: -4px;
}
.header-grid {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 0px;
}
.header-logo-icon img {
  width: 85px;
}
.header-login-box a {
  display: inline-block;
  background: #22252c;
  padding: 10px 20px;
  font-size: 14px;
  color: #fff;
  border-radius: 5px;
  min-width: 120px;
  text-align: center;
}
.header-login-box a:hover {
  text-decoration: none;
  color: #fff;
}
.header-login-box span {
  margin-left: 20px;
  display: inline-block;
}
.header-login-box span:first-child a {
  background: #b9a11e;
}
.header .auth-btns a,
.header .auth-btns a:hover,
.header .auth-btns a:focus {
  padding: 2px 20px;
  gap: 10px;
  width: fit-content;
  height: 30px;
  border-radius: 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #ffffff;
  outline: none;
  box-shadow: none;
  text-decoration: none;
}
.header .auth-btns .signup-btn {
  background: #b9a11e;
  border: 2px solid #b9a11e;
}
.header .auth-btns .login-btn {
  background: transparent;
  border: none;
}
.header .navbar-expand-lg .navbar-collapse {
  justify-content: space-between;
  align-items: center;
}
.content-wrapper {
  padding-top: 77px;
}
.header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 0px;
}
.main-header-wrapper {
  display: none;
}
.header-top-login ul li span i {
  display: none;
}
.header-top-login ul li span p {
  margin: 0;
}
.language-dropdown .btn-primary,
.language-dropdown .btn-primary:hover,
.language-dropdown .btn-primary:focus,
.language-dropdown .show > .btn-primary.dropdown-toggle,
.language-dropdown .show > .btn-primary.dropdown-toggle:focus,
.language-dropdown .btn-primary:not(:disabled):not(.disabled):active,
.language-dropdown .btn-primary:not(:disabled):not(.disabled):active:focus,
.language-dropdown .show > .btn-primary.dropdown-toggle {
  background: transparent;
  color: transparent;
  border: none;
  box-shadow: none;
  padding: 0;
}
.language-dropdown button img {
  width: 25px;
  height: 25px;
  border-radius: 100%;
  object-fit: cover;
}
.language-dropdown .dropdown-menu {
  background: #1a1c22;
  border: 1px solid #b9a11e;
  box-shadow: none;
  border-radius: 10px;
}

.language-dropdown .dropdown-item img {
  width: 20px;
  height: 20px;
}
.language-dropdown .dropdown-item {
  display: flex;
  align-items: center;
  gap: 4px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
  padding: 8px 20px;
}
.language-dropdown .dropdown-item:hover {
  background-color: #b9a11e;
  color: #ffffff;
}
.header-wrapper-right {
  display: flex;
  align-items: center;
  gap: 12px;
}

@media screen and (max-width: 1440px) {
  .notifications {
    right: 75px !important;
  }
}
@media screen and (max-width: 992px) {
  .content-wrapper {
    padding-top: 45px;
  }
  .header-top-login ul li span i {
    display: block;
    margin: 0px auto;
    background: transparent;
    color: white;
    font-size: 24px;
    box-shadow: none;
  }
  .header-top-login ul li span p {
    display: none !important;
  }
  .header .header-top-login ul li span {
    min-width: fit-content;
    padding: 5px 5px;
    display: flex;
    justify-content: center;
    background: transparent;
    box-shadow: none;
    border: none;
  }
  .auth-btns {
    display: none;
  }
  .header .logo {
    display: none;
  }
  .main-header-wrapper {
    display: flex;
    justify-content: flex-end;
  }
  .main-header-wrapper .auth-btns,
  .main-header-wrapper .logo {
    display: block;
  }
  .header.help-center-header .auth-btns,
  .header.help-center-header .logo {
    display: block;
  }
  .header .navbar-expand-lg .navbar-collapse {
    flex-basis: fit-content;
  }
  .header-top-login ul li {
    margin: 0;
    margin-left: 15px;
  }
  .header-top-login ul {
    justify-content: flex-end;
  }
}
@media screen and (max-width: 767px) {
  .header-logo-icon img {
    width: 75px;
  }
}
@media screen and (max-width: 480px) {
  .header .navbar {
    padding: 8px 0px;
  }
  .content-wrapper {
    padding-top: 45px;
  }
}
